.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  /* font-size: calc(10px + 2vmin); */
  color: white;
  print-color-adjust: exact;
}

.App-rows {
  text-align: left;
  margin-left: 2em;
  margin-right: 2em;
}

.App-rows p {
  margin-top: 1px;
  margin-bottom: 1em;
}

.App-row {
  /*display: inline-block;
  min-width: 8em;*/
  vertical-align: top;
}

.App-run {
  display: inline-block;
  text-align: center;
  min-width: 2em;
  border: 2px solid white;
}

.selected:before {
  content:"\25b6";
}
.selected:after {
  content:"\25c0";
}

.App-grid {
  margin-top: 2em;
  image-rendering: "pixelated";
}
